@import url(https://fonts.googleapis.com/css?family=Lato:100,300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App:before {
  text-align: center;
  min-height: 100vh;
  background-color:'transparent';
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/static/media/Head.ac75bc35.png) no-repeat center bottom;
  background-size: 120vmin ;
}

.panel{
  display:flex;
  margin: 50px;
  justify-content: center;
  text-align: center;
  font-size: 80px;

}

.panel span {
  top: 50%;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  
  
}

.panel.blue {
  background-color:transparent;
  margin-top:30vh;

}

.panel.turqoise {
  background-color: transparent;
  margin-top:30vh;
  font-size: 15vmin;

}

.panel.green {
  background-color: transparent;
  margin-top:30vh;
  font-size: 12vmin;
}

.panel.bordeaux {
  background-color: transparent;
}


